@use "mixin" as m;

.comp-daily {
    .section-inner {
        padding-top: 0 !important;
        background: rgba(#fff, 0.5);
        ul {
            li {
                padding: 5px;
                border-bottom: 1px solid var(--sky);
                a {
                    position: relative;
                    display: block;
                    transition: all 0.5s;
                    &.active {
                        background: var(--sky);
                        color: #fff;
                        .fa-arrow-down {
                            transform: translateY(-50%) rotate(180deg);
                        }
                    }
                    .fa-arrow-down {
                        position: absolute;
                        top: 50%;
                        right: 10px;
                        transform: translateY(-50%);
                        font-size: 1.3em;
                        transition: all 0.5s;
                    }
                    .date {
                        font-weight: 500;
                        margin-bottom: 10px;

                        span {
                            margin-right: 5px;
                            &.desc {
                                font-style: oblique;
                            }
                        }
                    }
                    .info {
                        img {
                            width: 50px;
                            display: inline-block;
                            vertical-align: middle;
                            margin-right: 10px;
                        }
                        span {
                            margin-right: 7px;
                            i {
                                margin-right: 2px;
                            }
                            em {
                                font-weight: 600;
                            }
                        }
                    } //span
                } //info
                .detail {
                    display: none;
                    color: #fff;
                    padding: 10px;
                    border-radius: 5px;
                    background: rgba(#274c6e, 0.6);
                } //detail
            } //li
        } //ul
    } //section-inner
} //comp-daily
