@media screen and (max-width: 900px) {
  .comp-map {
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    margin: 0 !important;
    transform: translateX(100%);
    transition: transform 0.5s;
  }
  .comp-map.active {
    transform: translateX(0%);
  }
}
.comp-map .btns {
  position: absolute;
  display: flex;
  top: calc(50% - 25px);
  left: -45px;
  width: 100px;
  height: 50px;
  border-radius: 25px;
  z-index: 1;
  background: #FFF;
  overflow: hidden;
}
.comp-map .btns button {
  width: 50px;
  height: 50px;
  font-size: 1.5em;
}
.comp-map .btns button:first-child {
  background: var(--color);
  color: #FFF;
}
@media screen and (min-width: 900px) {
  .comp-map .btns {
    display: none;
  }
}
.comp-map .section-inner {
  border-radius: 10px;
}
@media screen and (max-width: 900px) {
  .comp-map .section-inner {
    height: 400px;
  }
}
.comp-map .section-inner #map {
  filter: hue-rotate(40deg);
  mix-blend-mode: multiply;
  height: 100%;
}