html {
  --sky: #8ed1f1;
  --color: #4378a3;
}

body {
  color: var(--color);
  font-family: "Montserrat", "Noto Sans KR", sans-serif;
  font-size: 15px;
  text-decoration: none;
  font-weight: normal;
  font-style: normal;
}

body {
  height: 100vh;
  background: var(--sky);
}
body::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(../assets/img/weather-icos.gif);
  background-size: 900px;
  opacity: 0.3;
  animation: bg 5000s linear infinite;
}
@keyframes bg {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: -50000px -50000px;
  }
}

h1 {
  perspective: 700px;
  position: absolute;
  left: 50%;
  top: -2%;
  width: min(40%, 200px);
  transform: translate(-50%, -60%);
}
h1 img {
  width: 100%;
  opacity: 0.7;
  animation: rotate 5s infinite linear;
}
@keyframes rotate {
  0% {
    transform: rotateX(0deg);
  }
  50% {
    transform: rotateX(360deg);
  }
  100% {
    transform: rotateX(360deg);
  }
}

#root {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -48%);
  width: min(95%, 1100px);
  height: min(90%, 800px);
  background: rgba(255, 255, 255, 0.7);
  padding: 5px;
  box-sizing: border-box;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.5);
  animation: grow 1.5s infinite alternate linear;
}
@media screen and (min-width: 900px) {
  #root {
    min-height: 760px;
  }
}
@keyframes grow {
  0% {
    box-shadow: 0 0 5px 0px rgba(255, 255, 255, 0.3);
  }
  100% {
    box-shadow: 0 0 20px 5px rgba(255, 255, 255, 0.7);
  }
}
#root .app-inner {
  position: relative;
  height: 100%;
}
@media screen and (min-width: 900px) {
  #root .app-inner {
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
  }
}
@media screen and (max-width: 900px) {
  #root .app-inner {
    overflow: hidden;
    overflow-x: hidden;
    overflow-y: auto;
  }
}
@media screen and (min-width: 900px) {
  #root .app-inner section {
    width: 50%;
    height: 50%;
  }
}
@media screen and (max-width: 900px) {
  #root .app-inner section {
    margin: 20px 0;
  }
}
#root .app-inner section .section-inner {
  position: relative;
  padding: 5px;
  box-sizing: border-box;
  border-radius: 10px;
}
@media screen and (min-width: 900px) {
  #root .app-inner section .section-inner {
    height: 100%;
    overflow: auto;
  }
}
#root .app-inner section .section-inner h2 {
  text-transform: uppercase;
  font-weight: 600;
  padding: 5px 10px;
  background: var(--color);
  color: #fff;
  border-radius: 5px;
  top: 0;
}
@media screen and (min-width: 900px) {
  #root .app-inner section .section-inner h2 {
    position: sticky;
    top: 0px;
  }
}
#root .app-inner section .section-inner .preloader {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: var(--sky);
}
#root .app-inner section .section-inner .preloader .spinner {
  margin: 100px auto;
  width: 40px;
  height: 40px;
  position: relative;
}
#root .app-inner section .section-inner .preloader .cube1, #root .app-inner section .section-inner .preloader .cube2 {
  background-color: #4378a3;
  width: 15px;
  height: 15px;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: sk-cubemove 1.8s infinite ease-in-out;
  animation: sk-cubemove 1.8s infinite ease-in-out;
}
#root .app-inner section .section-inner .preloader .cube2 {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}
@-webkit-keyframes sk-cubemove {
  25% {
    -webkit-transform: translateX(42px) rotate(-90deg) scale(0.5);
  }
  50% {
    -webkit-transform: translateX(42px) translateY(42px) rotate(-180deg);
  }
  75% {
    -webkit-transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
  }
  100% {
    -webkit-transform: rotate(-360deg);
  }
}
@keyframes sk-cubemove {
  25% {
    transform: translateX(42px) rotate(-90deg) scale(0.5);
    -webkit-transform: translateX(42px) rotate(-90deg) scale(0.5);
  }
  50% {
    transform: translateX(42px) translateY(42px) rotate(-179deg);
    -webkit-transform: translateX(42px) translateY(42px) rotate(-179deg);
  }
  50.1% {
    transform: translateX(42px) translateY(42px) rotate(-180deg);
    -webkit-transform: translateX(42px) translateY(42px) rotate(-180deg);
  }
  75% {
    transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
    -webkit-transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
  }
  100% {
    transform: rotate(-360deg);
    -webkit-transform: rotate(-360deg);
  }
}

@media (hover: hover) {
  body::-webkit-scrollbar-track,
  .app-inner::-webkit-scrollbar-track,
  .section-inner::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.1);
    background-color: #fff;
  }
  body::-webkit-scrollbar,
  .app-inner::-webkit-scrollbar,
  .section-inner::-webkit-scrollbar {
    width: 6px;
    height: 6px;
    background-color: #f5f5f5;
  }
  body::-webkit-scrollbar-thumb,
  .app-inner::-webkit-scrollbar-thumb,
  .section-inner::-webkit-scrollbar-thumb {
    background-color: var(--color);
  }
}