@use "mixin" as m;

section.comp-current {
    @media screen and (max-width: 900px) {
        margin-top: 30px !important;
    } //900
    .section-inner {
        .current-output {
            position: relative;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            box-sizing: border-box;
            border-radius: 10px;
            border: 1px solid #fff;
            color: #fff;
            background-size: cover;
            background-position: center;

            button.reload {
                position: absolute;
                width: 40px;
                height: 40px;
                right: 5%;
                top: 15%;
                font-size: 1.5em;
                opacity: 0.7;
                transition: all 0.3s;
                &:active,
                &:hover {
                    opacity: 1;
                    transform: rotate(180deg);
                }
            }
            @media screen and (min-width: 900px) {
                height: 100%;
            } //900이상
            @media screen and (max-width: 900px) {
                padding: 10px;
            } //900이하
            address {
                display: flex;
                margin-bottom: 10px;
                overflow: hidden;
                width: min(90%, 350px);
                span {
                    display: inline-block;
                    white-space: nowrap;
                    background: rgba(#fff, 0.2);
                    box-sizing: border-box;
                    padding: 3px;
                    font-size: 0.9em;
                    font-weight: 500;
                    animation: marquee 25s linear infinite;
                } //span
            } //address
            .cicle-container {
                position: relative;
                display: flex;
                align-items: center;
                justify-content: center;
                @include m.square;
                width: min(85%, 300px);
                border-radius: 50%;
                background: rgba(#000, 0.2);
                .sunset {
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    left: 0;
                    top: 0;
                    svg {
                        // border: 1px solid #FFF;
                        width: 100%;
                        .sun {
                            fill: rgb(255, 196, 0);
                            offset-path: path("M25,200c0-96.65,78.35-175,175-175s175,78.35,175,175");
                            // offset-distance: 50%;
                            animation: rotatesun 12s infinite linear;
                            @keyframes rotatesun {
                                0% {
                                    transform: rotate(0deg) scale(0.5);
                                }
                                50% {
                                    transform: rotate(360deg) scale(0.8);
                                }
                                100% {
                                    transform: rotate(720deg) scale(0.6);
                                }
                            }
                        }
                        .line {
                            fill: none;
                            stroke: rgb(#ddd, 0.3);
                            stroke-width: 3px;
                        }
                        .dash {
                            fill: none;
                            stroke: rgb(255, 196, 0);
                            // text-shadow: 0 0 20px 5px rgba(255, 196, 0);
                            stroke-width: 4px;
                            stroke-dasharray: 1;
                        }
                    } //svg
                    time {
                        display: inline-block;
                        font-size: 0.85em;
                        background: rgb(255, 196, 0);
                        padding: 3px 5px;
                        border-radius: 10px;
                        // small {
                        //     font-size: 0.8em;
                        //     font-weight: 600;
                        //     margin-right: 2px;
                        // }
                        &.risetime {
                            transform: translateX(-23%);
                        }
                        &.settime {
                            transform: translateX(23%);
                            float: right;
                        }
                    } //time
                } //sunset
                .circle-container-inner {
                    @include m.posLT(absolute, 50%, 50%, -50%, -50%);
                    width: 90%;
                    .temp {
                        position: relative;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        // .feel {
                        //     font-size: 0.8em;
                        // }
                        span {
                            position: absolute;
                            transform: translate(75px, 20px);
                            i {
                                margin-right: 2px;
                            }
                        }
                        em {
                            position: relative;
                            font-size: 2.5em;
                            margin-right: -60px;
                            z-index: 1;
                            text-shadow: 1px 1px 10px #fff;
                            sup {
                                font-size: 0.7em;
                            }
                        } //em 글자
                        img { 
                            width: 100px;
                            mask-image: url(../assets/mask.png);
                            mask-size: 100%;
                            mask-position: center;
                            mask-repeat: no-repeat;
                        } //img
                    } //temp
                    .desc {
                        text-align: center;
                        font-size: 1.2em;
                        margin-top: -20px;
                    }
                    .etc {
                        width: 62%;
                        margin: 3px auto;
                        text-align: center;
                        box-sizing: border-box;
                        padding: 8px 17px;
                        border-radius: 10px;
                        background: rgba(#000, 0.3);
                        span {
                            font-size: 1em;
                            margin: 0.5px;
                            display: inline-block;
                            i {
                                margin-right: 2px;
                                font-size: 0.8em;
                                &.fa-location-arrow {
                                    margin-right: 7px;
                                }
                            } //i
                            b {
                                margin-left: 1px;
                            } //b
                            small {
                                margin-right: 4px;
                                font-size: 0.8em;
                            } //small
                        } //span
                    } //etc
                    .time-date {
                        margin-top: -5px;
                        color: #fff;
                        text-align: center;
                        .time {
                            display: block;
                            &.time {
                                font-size: 1.2em;
                                small{font-size: 0.7em;}
                            }
                        } //time
                        .date {
                            font-size: 1.3em;
                        }
                    } //time-date
                } //circle-container-inner
            } //cicle-container
        } //current-output
    } //section-inner
} //comp-current

/* 주소 옆으로 */
@keyframes marquee {
    0% {
        transform: translateX(0);
    }

    100% {
        transform: translateX(-100%);
    }
}
/* 주소 옆으로 */
