@use "mixin" as m;

.comp-hourly {
  .section-inner {
    padding-top: 0 !important;
    overflow: auto;
    h2 {
      position: sticky;
      left: 0;
      z-index: 10;
    }
    @media screen and (min-width: 900px) {
      display: flex;
      flex-direction: column;
    } //
    .wrap {
      position: relative;
      display: inline-table;
      @media screen and (min-width: 900px) {
        height: 100%;
      } //
      @media screen and (max-width: 900px) {
        height: 400px;
      } //
      ul {
        display: inline-flex;
        height: 100%;
        li {
          padding-top: 5px;
          width: 70px;
          height: 100%;
          &:nth-child(even) {
            background: rgba(#fff, 0.5);
          }
          p {
            margin: 3px 0;
            font-size: 0.9em;
            img {
              display: inline-block;
              width: 80%;
            }
            text-align: center;
          }
        } //li
      } //ul
      .chart-wrap {
        position: absolute;
        width: calc(100% - 35px);
        // left: 30px;
        bottom: 50px;
        height: 50%;
        // background: darkblue;
      }
    } //div wrap
  } //section-inner
} //comp-hourly
