@use "mixin" as m;

.comp-map {
    @media screen and (max-width: 900px) {
        position: fixed;
        z-index: 1;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        margin: 0 !important;
        transform:  translateX(100%);
        transition: transform .5s;
        &.active{
            transform: translateX(0%);
        }
    }
    .btns {
        position: absolute;
        display: flex;
        top: calc(50% - 25px);
        left: -45px;
        width: 100px;
        height: 50px;
        border-radius: 25px;
        z-index: 1;
        background: #FFF;
        overflow: hidden;
        button{
            width: 50px;
            height: 50px;
            font-size: 1.5em;
            &:first-child{
                background: var(--color);
                color: #FFF;
            }
            // &:last-child{
            //     color: var(--color);
            // }
        }
        @media screen and (min-width: 900px) {
            display: none;
        }
    }
    .section-inner {
        border-radius: 10px;
        @media screen and (max-width: 900px) {
            height: 400px;
        }
        #map {
            filter: hue-rotate(40deg);
            mix-blend-mode: multiply;
            height: 100%;
        }
    } //map
} //comp-map
