@use "mixin" as m;

html {
  --sky: #8ed1f1;
  --color: #4378a3;
}

body {
  color: var(--color);
  font-family: "Montserrat", "Noto Sans KR", sans-serif;
  font-size: 15px;
  text-decoration: none;
  font-weight: normal;
  font-style: normal;
}

body {
  height: 100vh;
  background: var(--sky);
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url(../assets/img/weather-icos.gif);
    background-size: 900px;
    opacity: 0.3;
    animation: bg 5000s linear infinite;
    @keyframes bg {
      0% {
        background-position: 0 0;
      }
      100% {
        background-position: -50000px -50000px;
      }
    }
  } //before
} //body

h1 {
  perspective: 700px;
  position: absolute;
  left: 50%;
  top: -2%;
  width: min(40%, 200px);
  transform: translate(-50%,-60%);
  img {
    width: 100%;
    opacity: 0.7;
    animation: rotate 5s infinite linear;
  }
  @keyframes rotate {
    0%{transform: rotateX(0deg)}
    50%{transform: rotateX(360deg)}
    100%{transform: rotateX(360deg)}
  }
}

#root {
  @include m.posLT(fixed, 50%, 50%, -50%, -48%);
  width: min(95%, 1100px);
  height: min(90%, 800px);
  background: rgba(#fff, 0.7);
  padding: 5px;
  box-sizing: border-box;
  border-radius: 10px;
  border: 1px solid rgba(#fff, 0.5);
  animation: grow 1.5s infinite alternate linear;
  @media screen and (min-width: 900px) {
    min-height: 760px;
  }
  @keyframes grow {
    0% {
      box-shadow: 0 0 5px 0px rgba(#fff, 0.3);
    }
    100% {
      box-shadow: 0 0 20px 5px rgba(#fff, 0.7);
    }
  }
  .app-inner {
    position: relative;
    height: 100%;
    @media screen and (min-width: 900px) {
      display: flex;
      flex-wrap: wrap;
      overflow: hidden;
    }
    @media screen and (max-width: 900px) {
      overflow: hidden;
      overflow-x: hidden;
      overflow-y: auto;
    }
    section {
      @media screen and (min-width: 900px) {
        width: 50%;
        height: 50%;
      } //section-inner min 900
      @media screen and (max-width: 900px) {
        margin: 20px 0;
      } //section-inner max 900
      .section-inner {
        position: relative;
        padding: 5px;
        box-sizing: border-box;
        border-radius: 10px;
        @media screen and (min-width: 900px) {
          height: 100%;
          overflow: auto;
        }
        h2{
          text-transform: uppercase;
          font-weight: 600;
          padding: 5px 10px;
          background: var(--color);
          color: #fff;
          border-radius: 5px;
          top: 0;
          @media screen and (min-width:900px) {
            position: sticky;
            top:0px;
          }
        }//h2
        .preloader{
          position: absolute;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 100%;
          height: 100%;
          left: 0;
          top: 0;
          background: var(--sky);
          .spinner {
            margin: 100px auto;
            width: 40px;
            height: 40px;
            position: relative;
          }
          
          .cube1, .cube2 {
            background-color: #4378a3;
            width: 15px;
            height: 15px;
            position: absolute;
            top: 0;
            left: 0;
            
            -webkit-animation: sk-cubemove 1.8s infinite ease-in-out;
            animation: sk-cubemove 1.8s infinite ease-in-out;
          }
          
          .cube2 {
            -webkit-animation-delay: -0.9s;
            animation-delay: -0.9s;
          }
          
          @-webkit-keyframes sk-cubemove {
            25% { -webkit-transform: translateX(42px) rotate(-90deg) scale(0.5) }
            50% { -webkit-transform: translateX(42px) translateY(42px) rotate(-180deg) }
            75% { -webkit-transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5) }
            100% { -webkit-transform: rotate(-360deg) }
          }
          
          @keyframes sk-cubemove {
            25% { 
              transform: translateX(42px) rotate(-90deg) scale(0.5);
              -webkit-transform: translateX(42px) rotate(-90deg) scale(0.5);
            } 50% { 
              transform: translateX(42px) translateY(42px) rotate(-179deg);
              -webkit-transform: translateX(42px) translateY(42px) rotate(-179deg);
            } 50.1% { 
              transform: translateX(42px) translateY(42px) rotate(-180deg);
              -webkit-transform: translateX(42px) translateY(42px) rotate(-180deg);
            } 75% { 
              transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
              -webkit-transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
            } 100% { 
              transform: rotate(-360deg);
              -webkit-transform: rotate(-360deg);
            }
          }
        }
      } //section-inner
    } //section
  } //app-inner
} //root
body,
.app-inner,
.section-inner {
  @media (hover: hover) {
    &::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.1);
      background-color: #fff;
    }
    &::-webkit-scrollbar {
      width: 6px;
      height: 6px;
      background-color: #f5f5f5;
    }
    &::-webkit-scrollbar-thumb {
      background-color: var(--color);
    }
  } //media hover
}
