.comp-daily .section-inner {
  padding-top: 0 !important;
  background: rgba(255, 255, 255, 0.5);
}
.comp-daily .section-inner ul li {
  padding: 5px;
  border-bottom: 1px solid var(--sky);
}
.comp-daily .section-inner ul li a {
  position: relative;
  display: block;
  transition: all 0.5s;
}
.comp-daily .section-inner ul li a.active {
  background: var(--sky);
  color: #fff;
}
.comp-daily .section-inner ul li a.active .fa-arrow-down {
  transform: translateY(-50%) rotate(180deg);
}
.comp-daily .section-inner ul li a .fa-arrow-down {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  font-size: 1.3em;
  transition: all 0.5s;
}
.comp-daily .section-inner ul li a .date {
  font-weight: 500;
  margin-bottom: 10px;
}
.comp-daily .section-inner ul li a .date span {
  margin-right: 5px;
}
.comp-daily .section-inner ul li a .date span.desc {
  font-style: oblique;
}
.comp-daily .section-inner ul li a .info img {
  width: 50px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
}
.comp-daily .section-inner ul li a .info span {
  margin-right: 7px;
}
.comp-daily .section-inner ul li a .info span i {
  margin-right: 2px;
}
.comp-daily .section-inner ul li a .info span em {
  font-weight: 600;
}
.comp-daily .section-inner ul li .detail {
  display: none;
  color: #fff;
  padding: 10px;
  border-radius: 5px;
  background: rgba(39, 76, 110, 0.6);
}