@charset "UTF-8";
@media screen and (max-width: 900px) {
  section.comp-current {
    margin-top: 30px !important;
  }
}
section.comp-current .section-inner .current-output {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  border-radius: 10px;
  border: 1px solid #fff;
  color: #fff;
  background-size: cover;
  background-position: center;
}
section.comp-current .section-inner .current-output button.reload {
  position: absolute;
  width: 40px;
  height: 40px;
  right: 5%;
  top: 15%;
  font-size: 1.5em;
  opacity: 0.7;
  transition: all 0.3s;
}
section.comp-current .section-inner .current-output button.reload:active, section.comp-current .section-inner .current-output button.reload:hover {
  opacity: 1;
  transform: rotate(180deg);
}
@media screen and (min-width: 900px) {
  section.comp-current .section-inner .current-output {
    height: 100%;
  }
}
@media screen and (max-width: 900px) {
  section.comp-current .section-inner .current-output {
    padding: 10px;
  }
}
section.comp-current .section-inner .current-output address {
  display: flex;
  margin-bottom: 10px;
  overflow: hidden;
  width: min(90%, 350px);
}
section.comp-current .section-inner .current-output address span {
  display: inline-block;
  white-space: nowrap;
  background: rgba(255, 255, 255, 0.2);
  box-sizing: border-box;
  padding: 3px;
  font-size: 0.9em;
  font-weight: 500;
  animation: marquee 25s linear infinite;
}
section.comp-current .section-inner .current-output .cicle-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: min(85%, 300px);
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.2);
}
section.comp-current .section-inner .current-output .cicle-container::before {
  content: "";
  display: block;
  padding-top: 100%;
}
section.comp-current .section-inner .current-output .cicle-container .sunset {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}
section.comp-current .section-inner .current-output .cicle-container .sunset svg {
  width: 100%;
}
section.comp-current .section-inner .current-output .cicle-container .sunset svg .sun {
  fill: rgb(255, 196, 0);
  offset-path: path("M25,200c0-96.65,78.35-175,175-175s175,78.35,175,175");
  animation: rotatesun 12s infinite linear;
}
@keyframes rotatesun {
  0% {
    transform: rotate(0deg) scale(0.5);
  }
  50% {
    transform: rotate(360deg) scale(0.8);
  }
  100% {
    transform: rotate(720deg) scale(0.6);
  }
}
section.comp-current .section-inner .current-output .cicle-container .sunset svg .line {
  fill: none;
  stroke: rgba(221, 221, 221, 0.3);
  stroke-width: 3px;
}
section.comp-current .section-inner .current-output .cicle-container .sunset svg .dash {
  fill: none;
  stroke: rgb(255, 196, 0);
  stroke-width: 4px;
  stroke-dasharray: 1;
}
section.comp-current .section-inner .current-output .cicle-container .sunset time {
  display: inline-block;
  font-size: 0.85em;
  background: rgb(255, 196, 0);
  padding: 3px 5px;
  border-radius: 10px;
}
section.comp-current .section-inner .current-output .cicle-container .sunset time.risetime {
  transform: translateX(-23%);
}
section.comp-current .section-inner .current-output .cicle-container .sunset time.settime {
  transform: translateX(23%);
  float: right;
}
section.comp-current .section-inner .current-output .cicle-container .circle-container-inner {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
}
section.comp-current .section-inner .current-output .cicle-container .circle-container-inner .temp {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
section.comp-current .section-inner .current-output .cicle-container .circle-container-inner .temp span {
  position: absolute;
  transform: translate(75px, 20px);
}
section.comp-current .section-inner .current-output .cicle-container .circle-container-inner .temp span i {
  margin-right: 2px;
}
section.comp-current .section-inner .current-output .cicle-container .circle-container-inner .temp em {
  position: relative;
  font-size: 2.5em;
  margin-right: -60px;
  z-index: 1;
  text-shadow: 1px 1px 10px #fff;
}
section.comp-current .section-inner .current-output .cicle-container .circle-container-inner .temp em sup {
  font-size: 0.7em;
}
section.comp-current .section-inner .current-output .cicle-container .circle-container-inner .temp img {
  width: 100px;
  mask-image: url(../assets/mask.png);
  mask-size: 100%;
  mask-position: center;
  mask-repeat: no-repeat;
}
section.comp-current .section-inner .current-output .cicle-container .circle-container-inner .desc {
  text-align: center;
  font-size: 1.2em;
  margin-top: -20px;
}
section.comp-current .section-inner .current-output .cicle-container .circle-container-inner .etc {
  width: 62%;
  margin: 3px auto;
  text-align: center;
  box-sizing: border-box;
  padding: 8px 17px;
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.3);
}
section.comp-current .section-inner .current-output .cicle-container .circle-container-inner .etc span {
  font-size: 1em;
  margin: 0.5px;
  display: inline-block;
}
section.comp-current .section-inner .current-output .cicle-container .circle-container-inner .etc span i {
  margin-right: 2px;
  font-size: 0.8em;
}
section.comp-current .section-inner .current-output .cicle-container .circle-container-inner .etc span i.fa-location-arrow {
  margin-right: 7px;
}
section.comp-current .section-inner .current-output .cicle-container .circle-container-inner .etc span b {
  margin-left: 1px;
}
section.comp-current .section-inner .current-output .cicle-container .circle-container-inner .etc span small {
  margin-right: 4px;
  font-size: 0.8em;
}
section.comp-current .section-inner .current-output .cicle-container .circle-container-inner .time-date {
  margin-top: -5px;
  color: #fff;
  text-align: center;
}
section.comp-current .section-inner .current-output .cicle-container .circle-container-inner .time-date .time {
  display: block;
}
section.comp-current .section-inner .current-output .cicle-container .circle-container-inner .time-date .time.time {
  font-size: 1.2em;
}
section.comp-current .section-inner .current-output .cicle-container .circle-container-inner .time-date .time.time small {
  font-size: 0.7em;
}
section.comp-current .section-inner .current-output .cicle-container .circle-container-inner .time-date .date {
  font-size: 1.3em;
}

/* 주소 옆으로 */
@keyframes marquee {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}
/* 주소 옆으로 */